import Button from "@material-ui/core/Button"

export default function SharePage(){
    function copyToClipboard() {
        const text = "https://mahaaad.github.io/mahadflix/i2303..."

        if (window.clipboardData && window.clipboardData.setData) {
            // Internet Explorer-specific code path to prevent textarea being shown while dialog is visible.
            return window.clipboardData.setData("Text", text);
    
        }
        else if (document.queryCommandSupported && document.queryCommandSupported("copy")) {
            var textarea = document.createElement("textarea");
            textarea.textContent = text;
            textarea.style.position = "fixed";  // Prevent scrolling to bottom of page in Microsoft Edge.
            document.body.appendChild(textarea);
            textarea.select();
            try {
                return document.execCommand("copy");  // Security exception may be thrown by some browsers.
            }
            catch (ex) {
                console.warn("Copy to clipboard failed.", ex);
                return false;
            }
            finally {
                document.body.removeChild(textarea);
            }
        }
    }
    return(
    <div className="share-page">
        <div className="share-page-title-container">
            <img className="chat-bubble-icon" src="/resources/img/chat-bubble.svg"/>
            <h2 className="share-page-title">Tell friends about MahadFlix.</h2>
        </div>
        <div className="share-container">
            <p className="share-info">Share this link so your friends can join the conversation around all your favorite TV shows and movies.</p>
            <div className="share-links">
                <p className="share-url">
                    https://mahaaad.github.io/mahadflix/i2303...
                    <Button onClick={copyToClipboard}variant="contained"style={{float: "right", color: "white", backgroundColor: "red", marginLeft: "1rem"}}>
                        Copy Link
                    </Button>
                </p>
                <button className="share-link-button" data-tooltip="email">
                    <img src="/resources/img/mail-icon.png"/>
                </button>
                <button className="share-link-button" data-tooltip="whatsapp">
                    <img src="/resources/img/whatsapp-icon.svg"/>
                </button>
                <button className="share-link-button" data-tooltip="facebook">
                    <img src="/resources/img/facebook-icon.svg"/>
                </button>
                <button className="share-link-button" data-tooltip="twitter">
                    <img src="/resources/img/twitter-icon.svg"/>
                </button>
                <button className="share-link-button" data-tooltip="messenger">
                    <img src="/resources/img/messenger-icon.svg"/>
                </button>
            </div>
        </div>

        <h3 className="how-it-works">HOW IT WORKS</h3>
            <div className="share-steps-container">
                <div className="share-step">
                    <img src="/resources/img/share-icon.svg"/>
                    <p>
                        <span>Step 1</span><br/>
                        Share your link with friends by<br/> copying the link or choose an icon.
                    </p>
                </div>
                
                <div className="share-step">
                    <img src="/resources/img/chat-bubble.svg"/>
                    <p>
                        <span>Step 2</span><br/>
                        We’ll let you know when a friend<br/> signs up.
                    </p>
                </div>
                
                <div className="share-step">
                    <img src="/resources/img/chat-bubble.svg"/>
                    <p>
                        <span>Step 3</span><br/>
                        You can share with as many friends<br/> and family as you like!
                    </p>
                </div>
            </div>
    </div>
    )
}