import React, {useState} from "react"

export default function Header(){
    const [notificationsVisibility, setNotificationsVisibility] = useState("none")
    const [notificationsOpacity, setNotificationsOpacity] = useState("0")

    const [accountMenuVisibility, setAccountMenuVisibility] = useState("none")
    const [accountMenuOpacity, setAccountMenuOpacity] = useState("0")
    
    function showNotifications(){
        setNotificationsOpacity("1")
        setNotificationsVisibility("visible")
    }
    function hideNotifications(){
        setNotificationsOpacity("0")
        setNotificationsVisibility("hidden")
    }
    function showAccountMenu(){
        setAccountMenuOpacity("1")
        setAccountMenuVisibility("visible")
    }
    function hideAccountMenu(){
        setAccountMenuOpacity("0")
        setAccountMenuVisibility("hidden")
    }
    return(
        <header className="App-header">
            <img className="mahadflix-logo" src="resources/img/mahadflix-logo.svg"/>
            <img className="gift-icon" src="resources/img/gift.svg" />
            <div className="notifications-container">
                <img className="bell-icon" src="resources/img/bellwhite.svg" onMouseEnter={showNotifications} onMouseLeave={hideNotifications} />
                <div className="notifications" style={{visibility: notificationsVisibility, opacity: notificationsOpacity}}>No new notifications</div>
            </div>

            <div className="account-container">
            <img className="profile-photo" src="resources/img/MahadRehan.jpg" />
                <div className="account-menu" style={{visibility: accountMenuVisibility, opacity: accountMenuOpacity}}>MahadR</div>
            </div>
            <div className="arrow" onMouseEnter={showAccountMenu} onMouseLeave={hideAccountMenu}></div>
        </header>
    )
}