import React, { useEffect } from "react"
import Aos from 'aos'
import 'aos/dist/aos.css'


export default function HomePage(){

  useEffect(() => {
    Aos.init({duration: 1500})
  }, [])
  return(
    <div className="home-page">
      <div className="gradient" style={{background: "radial-gradient(circle, rgba(0,0,0,0) 35%, rgba(0,0,0,0.70) 100%)"}}></div>
      <div className="home-page-title-container">
        <h1 className="home-page-title" data-aos="fade-up" data-aos-once="true" data-aos-delay="50">
          All your favourite titles on 
        </h1>
        <img className="home-page-title-logo" src="resources/img/mahadflix-logo.svg" data-aos="fade-up" data-aos-once="true" data-aos-delay="50"/>
      </div>
      <p className="home-page-description" data-aos="fade-up" data-aos-once="true" data-aos-delay="500" >
        Browse through your favourite movies and nominate your top 5
        flix for Shopify's movie award show. See the <span><img src="/resources/img/search.svg"/></span> tab to 
        browse for movies you would like to nominate then submit at the <span><img src="/resources/img/add.svg"/></span> tab.
      </p>
      <div className="dev-links">
        <a href="https://github.com/mahaaad" target="_blank">
          <img src="resources/img/githublogo.png"/>
        </a>
        <a href="https://mahaaad.github.io/website/" target="_blank">
          <img />
        </a>
      </div>
      <div className="home-page-background"></div>
    </div>
  )
}