import React, {useState, useEffect} from "react"
import Aos from 'aos'
import Button from "@material-ui/core/Button"
import 'aos/dist/aos.css'

import MovieInfo from "./MovieInfo"
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

export default function Movie(props){

    const [infoOpacity, setInfoOpacity] = useState("0")
    const [infoMargin, setInfoMargin] = useState("7.5vw")
    const [infoTransition, setInfoTransition] = useState(["1.5s","2s"])
    const [wallpaperOpacity, setWallpaperOpacity] = useState("1")
    const [nominated, setNominated] = useState(props.nom)
    const [message, setMessage] = useState(<p>You have nominated 5 films! <br/> See the submit tab to send in your picks.</p>)
    const [messageDisplay, setMessageDisplay] = useState(false)

    const handleClickOpen = () => {
        setMessageDisplay(true);
    };

    const handleClose = () => {
        setMessageDisplay(false);
    };

    let plot = String(props.plot)
    if (plot.length > 170){
        plot = plot.substring(0,169) + "..."
    }

    function showInfo(){
        setInfoTransition(["1s","1.5s"])
        setInfoOpacity("1")
        setInfoMargin("0")
        setWallpaperOpacity("0.25")
    }

    function hideInfo(){
        setInfoTransition(["0.75s","1s"])
        setInfoOpacity("0")
        setInfoMargin("7.5vw")
        setWallpaperOpacity("1")
    }

    function nominate(){
        let nominationsArray = JSON.parse(localStorage.getItem("nominated"))
        if(nominationsArray.length == 5){
            setMessage(<p>You have already nominated 5 films. <br /> See the Flix tab to edit your nominations.</p>)
            handleClickOpen()
        }
        else{
            if(nominationsArray.length == 4){
                setMessage(<p>You have nominated 5 films! <br/> See the submit tab to send in your picks.</p>)
                handleClickOpen()
            }
            nominationsArray.push(props.title)
            localStorage.setItem("nominated", JSON.stringify(nominationsArray))
            setNominated(true)
            setWallpaperOpacity("0.25")

        }
        hideInfo()
        setInfoMargin("-7.5vw")


        //console.log(JSON.parse(localStorage.getItem("nominated")))
    }

    function removeNomination(){
        let nominationsArray = JSON.parse(localStorage.getItem("nominated"))
        let index = nominationsArray.indexOf(props.title);
        if (index > -1){
            nominationsArray.splice(index, 1)
            localStorage.setItem("nominated", JSON.stringify(nominationsArray))
            setNominated(false)
            setWallpaperOpacity("1")
        }
    }

    useEffect(() => {
        Aos.init({duration: 1500})
    }, [])
    return( 
        <div className="movie" onMouseEnter={!nominated ? showInfo : ""} onMouseLeave={!nominated ? hideInfo : ""} disabled={nominated}>
            <Dialog className="alert-dialog"
                open={messageDisplay}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {message}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
            <div data-aos="fade-up" data-aos-once="true" data-aos-delay="50" style={{height: "27.5vw", width: "100%",}}>
                <div className="nominated" style={{display: nominated ? "flex" : "none"}}>
                    <span><img src="/resources/img/nominatewhite.svg"/>NOMINATED</span>
                    <Button onClick={removeNomination} className="nominate-button" color="secondary" variant="outlined" size="medium" style={{color: "white", fontWeight: "bold", marginTop: "5%", marginLeft: "2.5%"}}>
                        REMOVE NOMINATION
                    </Button>
                </div>
                <div className="hover-info" style={{marginTop: nominated ? "-7.5vw" : infoMargin, opacity: infoOpacity, transition: `margin-top ease-in-out ${infoTransition[0]}, opacity ease-in-out ${infoTransition[1]}`}}>
                    <Button onClick={nominate} className="nominate-button" color="secondary" variant="outlined" size="medium" style={{color: "white", fontWeight: "bold", marginTop: "5%",marginLeft: "2.5%"}}>
                        NOMINATE
                    </Button>
                    <h2 className="movie-title" >
                        {props.title} {props.year}
                    </h2>
                    <div className="movie-info">
                        <small className="imdb-rating" style={{ display: props.rated == "N/A" || props.rated == "Not Rated" ? `none ` : ` default`}}>★{props.rating}</small>
                        <small className="content-rating" style={{display: props.rating == "N/A" ? `none ` : ` default`}}>{props.rated}</small>
                        <small className="run-time" style={{display: props.runtime == "N/A" ? `none ` : ` default`}}>{props.runtime}</small>
                    </div>
                    <p className="movie-plot" style={{display: props.plot == "N/A" ? "none" : "default"}}>{plot}</p>
                </div>
                <img className="movie-wallpaper" src={props.wallpaper} style={{opacity: nominated ? "0.25" : wallpaperOpacity, transition: "ease-in-out 0.5s"}}/>
            </div> 
        </div>
    )
}