import React, { useState } from "react"
import {BrowserRouter as Router, HashRouter, Link} from "react-router-dom"

export default function NavBar(){

    const [iconsArray, setIconsArray] = useState([
        <Link to="/">
            <input onClick={()=>{setSelection(0)}} type="image" src="/resources/img/home.svg" className="nav-icon" key={0}/>
        </Link>,
        <div className="selected" style={{width: "100%"}} key={"selected"}></div>,
        <Link to="/Search">
            <input onClick={()=>{setSelection(1)}}  type="image" src="/resources/img/search.svg" className="nav-icon" key={1}/>
        </Link>,
        <Link to="/Flix">
            <input onClick={()=>{setSelection(2)}}  type="image" src="/resources/img/movie.svg" className="nav-icon" key={2}/>
        </Link>,
        <Link to="/">
            <input onClick={()=>{setSelection(3)}}  type="image" src="/resources/img/screen.svg" className="nav-icon" key={3}/>
        </Link>,
        <Link to="/">
            <input onClick={()=>{setSelection(4)}}  type="image" src="/resources/img/add.svg" className="nav-icon" key={4}/>
        </Link>,
    ])
    
    function setSelection(index){
        let selectIndex = -1
        for(let i = 0; i < iconsArray.length; i++){
            if(iconsArray[i].key == "selected"){
                selectIndex = i
                break
            }
        }
        let item = iconsArray[selectIndex]
        console.log(item)
        if (selectIndex > -1) {
            iconsArray.splice(selectIndex, 1);
        }
        iconsArray.splice(index+1, 0, item);

        setIconsArray([...iconsArray])
    }

    return(
        <div className="nav-bar">
            <HashRouter>
            {iconsArray}
            </HashRouter>
        </div>
    )
}