import React, { useState } from "react"
import Aos from 'aos'
import 'aos/dist/aos.css'

export default function FlixPage(){
  let array = JSON.parse(localStorage.getItem("nominated"))
  const [emptyMessage, setEmptyMessage] = useState("block")


    return(
      <div className="flix-page">
        <h1 className="flix-page-title" data-aos="fade-up" data-aos-once="true" >Nominated Flix</h1>
        <div className="nominated-flix-container"  data-aos="fade-up" data-aos-once="true" data-aos-delay="250">
          <ol className="nominated-list" >
            <li className="empty-flix" style={{fontSize: "3rem"}} data-aos="fade-up" data-aos-once="true" data-aos-delay="350"></li>
            <li className="empty-flix" style={{fontSize: "2.75rem"}} data-aos="fade-up" data-aos-once="true" data-aos-delay="450"></li>
            <li className="empty-flix" style={{fontSize: "2.5rem"}} data-aos="fade-up" data-aos-once="true" data-aos-delay="550"></li>
            <li className="empty-flix" data-aos="fade-up" data-aos-once="true" data-aos-delay="650"></li>
            <li className="empty-flix" data-aos="fade-up" data-aos-once="true" data-aos-delay="750"></li>
          </ol>
          <p style={{display: emptyMessage}}>
            Nominated movies will be shown here!<br/>
            See the search tab to find movies to nominate
          </p>
        </div>
      </div>
    )
}